// Home Boxs
.home_boxs {
	padding: 30px 0 40px;

	.col-GAP {
		display: flex;
		flex-direction: column;
		gap: 15px;
	}

	.spicific_col_gap {
		@include Sm {
			flex-direction: row;
			padding: 20px 0;
		}
	}

	.col-lg-6 {
		.pos {
			position: absolute;
			top: 0;
			right: 0;
			width: auto;
			padding: 0 5px;
			height: 28px;
			background-color: #f05921;
			z-index: 2;
			color: #fff;
			font-size: 13px;
			@extend .Flex;
		}

		.top,
		.bottom {
			position: relative;
			display: block;
		}

		.top {
			@include Sm {
				display: flex;
				justify-content: center;
				max-width: max-content;
				margin: auto;
			}
		}

		.bottom {
			.Arrows_top {
				position: absolute;
				top: 0;
				left: 0;
				display: flex;
				align-items: center;
				justify-content: space-between;
				width: 71px;
				height: 34px;
				flex-direction: row-reverse;
				z-index: 3;

				> div {
					width: 34px;
					height: 34px;
					background-color: var(--main-color);
					@extend .Flex;
					margin-top: 0;
					position: unset;
					opacity: 1 !important;

					&:after {
						font-size: 16px;
						color: #fff;
						// transform: scaleX(-1)
					}
				}
			}

			.over {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 100%;
				height: 50%;
				background: linear-gradient(
					to bottom,
					rgba(0, 0, 0, 0) 0%,
					rgba(0, 0, 0, 0.8) 100%
				);
				padding: 0 15px;
				text-align: center;
				display: flex;
				align-items: flex-end;
				padding-bottom: 30px;

				h2 {
					color: #fff;
					font-size: 14px;
					font-weight: 600;
				}
			}
		}

		.swiper-slide {
			img {
				@include Sm {
					width: 100%;
				}
			}
		}

		.swiper-pagination {
			bottom: 10px;
			span {
				background-color: #fff;
				opacity: 1 !important;

				&.swiper-pagination-bullet-active {
					background-color: var(--main-color);
				}
			}
		}

		.border_images {
			.top.hover {
				box-shadow: 0px 0px 6px rgba(#000, 0.16);
				padding: 10px;
				border-radius: 5px;
			}
		}
	}

	.col-lg-5 {
		.head {
			display: flex;
			align-items: center;
			justify-content: space-between;
			background-color: var(--main-color);
			padding: 5px 15px;
			margin-bottom: 20px;

			h3 {
				font-size: 16px;
				color: #fff;
				margin-bottom: 0;
			}

			a {
				display: inline-block;
				direction: ltr;
				color: #fff;
				font-size: 14px;
			}
		}

		.box {
			display: block;
			background-color: #f5f5f5;
			margin-bottom: 35px;
			padding-bottom: 10px;
			border-radius: 5px;
			overflow: hidden;

			@include Sm {
				margin-bottom: 0;
			}

			@include mob480 {
				width: 100%;
			}

			img {
				width: 100%;
				object-fit: cover;
				border-radius: 5px;
			}

			.btm_dts {
				margin-top: 15px;
				padding: 0 15px;

				h3 {
					color: #212121;
					font-size: 17px;
				}

				.date {
					color: #968c8b;
					font-size: 13px;
				}
			}
		}
	}

	.news_boxs {
		@include Sm {
			display: flex;
			gap: 15px;
			flex-wrap: wrap;
		}
	}
}
